@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
}

* {
  box-sizing: border-box;
  font-family: Verdana , Geneva , Tahoma , sans-serif;
  margin: 0;
  padding: 0;
}

.page-login-wrapper {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 80px;
}

.login-form {
  align-items: center;
  border-radius: 10px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  grid-gap: 30px;
  gap: 30px;
  margin-top: 80px;
  max-height: 314px;
  max-width: 697px;
  padding: 100px 85px 209px;
}

.login-form input {
  border: 1px solid #e1e5eb;
  height: 45px;
  padding: 12px 16px;
  width: 519px;
}

.login-form input::-webkit-input-placeholder {
  color: #6b7588;
  font-size: 14px;
}

.login-form input::placeholder {
  color: #6b7588;
  font-size: 14px;
}

.login-form button {
  background-color: #003be5;
  border: 1px solid #003be5;
  border-radius: 2px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  color: white;
  cursor: pointer;
  font-size: 16px;
  height: 40px;
  padding: 10px 32px;
  width: 519px;
}

.login-form button:hover {
  background-color: #002db4;
}

* {
  box-sizing: border-box;
  font-family: Epilogue , sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.header-container {
  display: flex;
  flex-direction: column;
}

.user-wrapper {
  align-items: center;
  background-color: #023031;
  box-shadow: 0 4px 1px rgba(0, 0, 0, 0.25);
  display: flex;
  height: 90px;
  justify-content: space-between;
  padding: 0 30px;
}

.user-wrapper img {
  height: 82px;
  margin-bottom: 30px;
}

.user-name {
  align-items: center;
  background-color: white;
  border-radius: 50px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  color: #29313d;
  display: flex;
  font-size: 18px;
  font-weight: 500;
  height: 41.65px;
  justify-content: center;
  width: 198.7px;
}

.nav-header-menu {
  align-items: center;
  background-color: #f0f2f5;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
  display: flex;
  font-size: 28px;
  font-weight: 700;
  height: 78px;
  justify-content: space-around;
}

.nav-header-menu a {
  color: #2fc18c;
}

.nav-header-menu span {
  align-items: center;
  border-right: solid 4px white;
  display: flex;
  height: 100%;
  justify-content: center;
  width: 100%;
}

* {
  box-sizing: border-box;
  color: #023031;
  font-family: Epilogue , sans-serif;
  margin: 0;
  padding: 0;
}

.notfound-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 150px;
}

.not-found-text-wrapper {
  display: flex;
  grid-gap: 50px;
  gap: 50px;
  margin-top: 120px;
  width: 1080px;
}

.alert1 {
  font-size: 13em;
  line-height: 205px;
}

.alert2 {
  font-size: 3.6em;
  font-weight: 300;
  line-height: 65px;
}

